
import { AppBar, Typography, TextField, InputAdornment, Button, Box, Icon, Hidden } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';
import brandImg from "../images/brandImg.svg";
import { useState} from 'react';
import { Autocomplete } from "@material-ui/core";

const options = []

const useCollapseButton = () => {
    const [collapseButton, setCollapseButton] = useState(false);
    const handleCollapseButton = () => setCollapseButton(!collapseButton)
    return [collapseButton, handleCollapseButton]
}

const Search = ({ collapseButton, handleOpen, size, only }) => {
    const [inputQuery, setInputQuery] = useState('');
    const handleInputChange = (e) => {
        setInputQuery(e.target.value);
        options.push({ item: e.target.value });
    }
    return (
        <Hidden mediaQuery only={only}>
            <Box class={`space-x-2 ${collapseButton ? 'hidden' : 'contents'}`} >
                <Autocomplete className={size}
                    freeSolo
                    options={options.map((option) => option.item)}
                    renderInput={(params) =>
                        <TextField {...params}
                            InputProps={{
                                ...params.InputProps,
                                size: 'small',
                                placeholder: 'Enter the Name of a City',
                                value: inputQuery,
                                onInput: handleInputChange,
                                margin: 'dense',
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <SearchIcon />
                                    </InputAdornment>
                                ),
                            }}
                            class={`w-full bg-white text-black rounded-xl `} />}
                />

                <Box className='flex space-x-2 mb-2 sm:mb-0'>
                    <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
                    <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
                </Box>
            </Box>
        </Hidden>
    )
}


function NavBar({ handleOpen }) {
    const [collapseButton, handleCollapseButton] = useCollapseButton();
    return (
        <AppBar class='bg-black text-white p-2 sticky w-full'>
            <Box id='nav-row' class='flex'>
                <Box class='flex space-x-1 items-center'>
                    <Icon fontSize='large'><img alt='brand visual' src={brandImg} /></Icon>
                    <Typography variant='h6'>API Weather App</Typography>
                </Box>
                <Box class='flex justify-center items-center flex-grow'>
                    <Search handleOpen={handleOpen} size='w-5/12 mx-2' only={['xs', 'sm']} />
                </Box>
                <Hidden mdUp>
                    <Box class='ml-auto self-center' component='button' onClick={handleCollapseButton} >
                        <MenuIcon color='white' fontSize='large' />
                    </Box>
                </Hidden>
            </Box>

            <Box class='flex justify-center items-center flex-grow'>
                <Search handleOpen={handleOpen} collapseButton={collapseButton} size='w-5/12' only={['xs', 'lg', 'md', 'xl']} />
            </Box>
            <Box class='flex flex-col-reverse justify-center items-center flex-grow'>
                <Search handleOpen={handleOpen} collapseButton={collapseButton} size='w-11/12' only={['sm', 'lg', 'md', 'xl']} />
            </Box>
        </AppBar>

        // <AppBar position='static'>
        //     <Box className='flex  w-full'>
        //     <div className=' flex flex-col lg:flex-row  p-2 lg:items-center bg-opacity-900 text-whit flex-grow'>
        //         <div className='flex items-center'>
        //         <div className='hidden lg:block'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
        //             <Typography variant='h6'>API Weather App</Typography>
        //         </div>
        //         <div id='menu' className='hidden  lg:flex bg-red-90 items-center sm:justify-center flex-col-reverse sm:flex-row flex-grow sm:space-x-2 space-y-2
        //         lg:space-y-0'>
        //             <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
        //                 InputProps={{
        //                     startAdornment: (
        //                       <InputAdornment position="start">
        //                         <SearchIcon />
        //                       </InputAdornment>
        //                     ),
        //                   }}
        //                 className='w-11/12 sm:w-7/12 lg:w-6/12 xl:w-5/12 bg-white text-black rounded-xl' />
        //             <div className='flex space-x-2 '>
        //                 <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
        //                 <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
        //             </div>
        //         </div>


        //         </div>
        //         <div className='lg:hidden text-white self-start ml-auto mt-1'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
        //         </Box>
        // </AppBar>
    )
};


export default NavBar;